.navigation {
  display: flex;
  flex: start;
  align-items: center;
  min-height: 80px;
  margin-bottom: 16px;
  border-bottom: 1px solid #2a363b;
}

.link {
  display: inline-block;
  text-decoration: none;
  padding: 12px;
  font-weight: 500;
  color: #2a363b;
  font-size: 18px;
  &:first-child {
    margin-left: 30px;
  }
}

.active_link {
  color: #2196f3;
}
